@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* === üåå GLOBAL STYLES === */
body {
  margin: 0;
  background-color: #0a0012;
  color: #bb88ff;
  font-family: 'Courier New', Courier, monospace;
  font-size: 15px;
  overflow: hidden;
  height: 100vh;
}

/* === üí° Flash When LOREA Speaks === */
body.flash {
  background-color: #8c00ff;
  transition: background-color 0.05s ease;
  animation: bodyFlashEffect 0.1s ease-out;
}

@keyframes bodyFlashEffect {
  0% {
    background-color: #ffffff;
    filter: brightness(200%) contrast(100%);
  }
  100% {
    background-color: #0a0012;
    filter: brightness(100%) contrast(100%);
  }
}


/* === üñ•Ô∏è Terminal Container === */
.terminal-screen {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  position: relative;
  background: #050011;
  padding: 1rem;
  box-sizing: border-box;
  overflow: hidden;
}

/* === üìú Terminal Output Scrollable Area === */
.terminal-output {
  flex-grow: 1;
  overflow-y: auto;
  white-space: pre-wrap;
  line-height: 0.9;
  animation: flicker 0.12s infinite alternate;
  z-index: 1;
}

.center-output {
  align-items: center;
  justify-content: center;
  display: flex;
}

/* === ü§ñ LOREA Thinking Text === */
.lorea {
  font-size: 1.2rem;
  color: #ffffff;
  text-align: center;
  text-shadow: 0 0 4px #cc66ff, 0 0 8px #8800cc;
  animation: fadeIn 1s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* === ‚å®Ô∏è Command Prompt Blinker === */
.prompt {
  color: #e0aaff;
  animation: blink 1s steps(1) infinite;
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
}

/* === ‚ú® Ambient Flicker === */
@keyframes flicker {
  0% { opacity: 0.97; }
  5% { opacity: 1; }
  10% { opacity: 0.93; }
  20% { opacity: 0.99; }
  30% { opacity: 0.96; }
  100% { opacity: 1; }
}

/* === üß† LOREA Glitch Effect Wrap === */
.glitch p {
  position: relative;
  display: block;
}

.glitch p::before,
.glitch p::after {
  content: attr(data-text);
  position: absolute;
  left: 0;
  width: 100%;
  overflow: hidden;
  background: transparent;
  clip: rect(0, 900px, 0, 0);
  opacity: 0.3;
}

.glitch p::before {
  animation: glitchTop 1.4s infinite linear alternate-reverse;
  color: #ff33cc;
}

.glitch p::after {
  animation: glitchBot 1.4s infinite linear alternate-reverse;
  color: #7209d48c;
}

/* === üîÄ Glitch Animation Frames === */
@keyframes glitchTop {
  0% { clip: rect(0, 9999px, 0, 0); }
  10% { clip: rect(4px, 9999px, 14px, 0); }
  20% { clip: rect(8px, 9999px, 18px, 0); }
  30% { clip: rect(3px, 9999px, 9px, 0); }
  100% { clip: rect(0, 9999px, 0, 0); }
}

@keyframes glitchBot {
  0% { clip: rect(0, 9999px, 0, 0); }
  10% { clip: rect(20px, 9999px, 35px, 0); }
  20% { clip: rect(15px, 9999px, 25px, 0); }
  30% { clip: rect(25px, 9999px, 40px, 0); }
  100% { clip: rect(0, 9999px, 0, 0); }
}

/* === üó£Ô∏è LOREA Dialogue Pulse === */
.lorea-speak {
  color: #e0b3ff;
  font-weight: bold;
  text-shadow: 0 0 4px #d17bff, 0 0 8px #6c00cc;
  animation: pulseGlow 1s ease-in-out infinite;
}

@keyframes pulseGlow {
  0% {
    text-shadow: 0 0 4px #d17bff, 0 0 8px #6c00cc;
    opacity: 0.9;
  }
  50% {
    text-shadow: 0 0 6px #f0c2ff, 0 0 12px #7a00ff;
    opacity: 1;
  }
  100% {
    text-shadow: 0 0 4px #d17bff, 0 0 8px #6c00cc;
    opacity: 0.9;
  }
}

/* === ‚ñã Terminal Cursor === */
.cursor {
  display: inline-block;
  width: 1ch;
  animation: blink 1s steps(1) infinite;
  color: #38005e;
}

.typing-line {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  /* REMOVE the border-right */
  /* border-right: 1px solid #e0b3ff; */
  width: 0;
  animation: typewriter 2.5s steps(30, end) forwards;
  font-family: 'Courier New', monospace;
  margin-top: 0.5rem;
  color: #4b007c;
  text-shadow: 0 0 2px #240038;
}


.typing-line.slower {
  animation: typewriter 4.5s steps(35, end) forwards;
}

@keyframes typewriter {
  from { width: 0 }
  to { width: 100% }
}

/* === üå´Ô∏è Subtle Breathing Pulse === */
.terminal-screen.thinking {
  animation: loreaPulse 3s ease-in-out infinite;
}

@keyframes loreaPulse {
  0%   { background-color: #050011; }
  50%  { background-color: #0a001a; }
  100% { background-color: #050011; }
}

/* === ‚ö° Enhanced Glitch Text Style === */
.glitch-text {
  position: relative;
  color: #e0b3ff;
  font-weight: bold;
  text-shadow: 0 0 3px #6c00cc, 0 0 6px #e0b3ff;
}

.glitch-text::before,
.glitch-text::after {
  content: attr(data-text);
  position: absolute;
  left: 0;
  width: 100%;
  pointer-events: none;
  opacity: 0.3;
  mix-blend-mode: lighten;
}

.glitch-text::before {
  color: #ff33cc;
  top: -1px;
  left: 2px;
  animation: glitchTop 0.8s infinite linear alternate-reverse;
}

.glitch-text::after {
  color: #66ffff;
  top: 1px;
  left: -2px;
  animation: glitchBot 0.8s infinite linear alternate-reverse;
}

/* === ‚ú® Flash Pulse After Speaking === */
.glitch-flash {
  animation: flashPulse 0.1s ease-out;
}

@keyframes flashPulse {
  0%   { filter: brightness(200%) contrast(180%); }
  50%  { filter: brightness(130%) contrast(130%); }
  100% { filter: brightness(100%) contrast(100%); }
}

/* === üì∫ CRT Scanline + Flicker Overlay === */
.terminal-screen::after {
  content: '';
  position: absolute;
  top: 0; left: 0;
  height: 100%; width: 100%;
  pointer-events: none;
  background: rgba(225, 0, 255, 0.02);
  mix-blend-mode: overlay;
  animation: screenFlicker 0.3s infinite;
  z-index: 2;
}

@keyframes screenFlicker {
  0%   { opacity: 0.01; }
  5%   { opacity: 0.03; }
  10%  { opacity: 0.015; }
  20%  { opacity: 0.04; }
  30%  { opacity: 0.01; }
  70%  { opacity: 0.025; }
  100% { opacity: 0.01; }
}

.terminal-screen::before {
  content: '';
  position: absolute;
  top: 0; left: 0;
  height: 100%; width: 100%;
  pointer-events: none;
  background: repeating-linear-gradient(
    to bottom,
    rgba(155, 3, 3, 0.04),
    rgba(255, 255, 255, 0.04) 1px,
    transparent 5px,
    transparent 2px
  );
  mix-blend-mode: hard-light;
  opacity: 40;
  z-index: 1;
}

/* === üíª App Wrapper === */
.app-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

/* === üßä Startup Fade Flash === */
.screen-on-overlay {
  position: absolute;
  z-index: 100;
  background: rgb(0, 0, 0);
  width: 100%;
  height: 100%;
  pointer-events: none;
  animation: screenOnStartup 4s ease-out forwards;
}

@keyframes screenOnStartup {
  0% {
    transform: scaleY(0.02);
    background-color: #030000;
    opacity: 1;
    filter: brightness(800%) blur(6px);
  }
  3% {
    transform: scaleY(0.2);
    background-color: #ffffff;
    opacity: 1;
    filter: brightness(300%) blur(2px);
  }
  12% {
    transform: scaleY(1.1);
    background-color: #000000;
    opacity: 0.7;
    filter: brightness(100%) blur(0px);
  }
  60% {
    transform: scaleY(1);
    opacity: 0.4;
  }
  90% {
    opacity: 0.1;
  }
  100% {
    opacity: 0;
    transform: scaleY(1);
    display: none;
  }
}

/* === üÜò No Signal Glitch Text === */
.no-signal-glitch {
  position: absolute;
  z-index: 200;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ff000d8a;
  font-family: 'Courier New', monospace;
  font-size: 1rem;
  letter-spacing: 0.2em;
  opacity: 1;
  animation: noSignalFlicker 3.2s ease-out forwards;
  text-shadow: 0 0 5px #ea00ff67, 0 0 10px #ff00bf54;
  pointer-events: none;
}

@keyframes noSignalFlicker {
  0% { opacity: 1; transform: translate(-50%, -50%) scale(1); }
  20% { opacity: 0.6; }
  30% { opacity: 0.9; }
  50% { opacity: 0.2; }
  70% { opacity: 0.8; }
  100% { opacity: 0; transform: translate(-50%, -50%) scale(1.2); }
}

/* === ‚ú® Initial Glow After Startup === */
.glow-start {
  animation: greenPhosphorGlow 2s ease-in;
}

@keyframes greenPhosphorGlow {
  0% {
    filter: brightness(140%) drop-shadow(0 0 8px #00ffcc);
  }
  100% {
    filter: brightness(100%) drop-shadow(0 0 0px transparent);
  }
}

/* === üïµÔ∏è Hide Terminal Until Boot === */
.hidden-terminal {
  visibility: hidden;
  opacity: 0;
}

/* === üì° Vertical Sync Scroll Effect === */
.vertical-sync {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 101;
  width: 100%;
  height: 100%;
  background: repeating-linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.02) 0px,
    rgba(255, 255, 255, 0.02) 2px,
    transparent 2px,
    transparent 4px
  );
  animation: syncScroll 1.5s linear infinite;
  pointer-events: none;
}

@keyframes syncScroll {
  0% {
    background-position-y: 0;
  }
  100% {
    background-position-y: 100%;
  }
}

/* === üîÑ Dots Loading === */
.loading-dots {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
  animation: fadeIn 1s ease-in;
}

.loading-dots span {
  width: 10px;
  height: 10px;
  margin: 0 5px;
  background: #8c00ff;
  border-radius: 50%;
  opacity: 0.1;
  animation: pulseDot 1.2s infinite;
}

.loading-dots span:nth-child(2) {
  animation-delay: 0.2s;
}
.loading-dots span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes pulseDot {
  0%, 100% {
    opacity: 0.1;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.5);
  }
}

/* === üß† LOREA Matrix Online Notice (Soft Glow) === */
.matrix-online-flash {
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #6c00cc;
  font-family: 'Courier New', monospace;
  font-size: 1.2rem;
  font-weight: bold;
  letter-spacing: 0.15em;
  white-space: nowrap; /* üß† Prevents line breaks */
  text-shadow: 0 0 4px #3600cc, 0 0 8px #6c00cc;
  pointer-events: none;
  animation: matrixSoftReveal 1.5s ease-out forwards, matrixTear 0.3s steps(2, end);
}

/* Soft pulse reveal */
@keyframes matrixSoftReveal {
  0% {
    opacity: 0;
    filter: brightness(140%) blur(1px);
    transform: translate(-50%, -50%) scale(0.98);
  }
  50% {
    opacity: 1;
    filter: brightness(160%) blur(0.5px);
    transform: translate(-50%, -50%) scale(1.02);
  }
  100% {
    opacity: 1;
    filter: brightness(100%) blur(0);
    transform: translate(-50%, -50%) scale(1);
  }
}

/* Quick glitch tear */
@keyframes matrixTear {
  0%, 100% {
    clip-path: inset(0% 0% 0% 0%);
  }
  50% {
    clip-path: inset(40% 0% 40% 0%);
  }
}
/* === üí• Flash after "Impossible" === */
.line-flash {
  animation: flashPulse 0.3s ease-out;
}

@keyframes flashPulse {
  0% {
    filter: brightness(220%) contrast(180%);
    transform: scale(1.6);
  }
  50% {
    filter: brightness(130%) contrast(140%);
    transform: scale(0.99);
  }
  100% {
    filter: brightness(100%) contrast(100%);
    transform: scale(2);
  }
}

/* === Intro Screen Container === */
.intro-sequence {
  background-color: #000000; /* DARK bunker black */
  color: #bb88ff;
  font-family: 'Courier New', monospace;
  font-size: 1.1rem;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}

/* === CRT Scanlines / Tear Overlay === */
.intro-sequence::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
  pointer-events: none;
  background: repeating-linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.02) 0px,
    rgba(255, 255, 255, 0.02) 2px,
    transparent 2px,
    transparent 4px
  );
  mix-blend-mode: overlay;
  animation: tearScroll 0.8s linear infinite;
}

@keyframes tearScroll {
  0% { background-position-y: 0; }
  100% { background-position-y: 100%; }
}

/* === CRT Glow Pulse / White Flash === */
.intro-sequence::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  z-index: 20;
  background: rgba(255, 255, 255, 0.151);
  mix-blend-mode: screen;
  animation: whiteFlash 9s infinite;
}

@keyframes whiteFlash {
  0%, 95%, 100% {
    opacity: 0;
    transform: scale(1);
  }
  96% {
    opacity: 0.12;
    transform: scale(1.05);
  }
  97% {
    opacity: 0.07;
    transform: scale(0.99);
  }
  98% {
    opacity: 0.1;
    transform: scale(1.02);
  }
  99% {
    opacity: 0;
    transform: scale(1);
  }
}

/* === Intro Line Style === */
.intro-line {
  position: absolute;
  text-align: center;
  max-width: 60ch;
  text-shadow: 0 0 4px #6c00cc, 0 0 8px #330066;
  opacity: 0;
  animation: smoothFade 4s ease-in-out forwards;
  will-change: opacity, transform;
}

/* === Fade In, Hold, Fade Out === */
@keyframes smoothFade {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  15% {
    opacity: 1;
    transform: translateY(0);
  }
  85% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
  }
}

/* === Ambient Flicker (barely noticeable) === */
@keyframes subtleFlicker {
  0% { opacity: 0.98; }
  50% { opacity: 1; }
  100% { opacity: 0.96; }
}

.timeline-container {
  opacity: 1;
  transition: opacity 0s ease-in-out;
  position: relative;
  width: 100%;
  height: 100vh;
  background: black;
  overflow: hidden;
  font-family: 'Share Tech Mono', monospace;
  color: #c7f0ff;
  z-index: 0;
}

.timeline-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;

  /* Subtle horizontal scanlines */
  background-image: repeating-linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.02),
    rgba(255, 255, 255, 0.02) 1px,
    transparent 1px,
    transparent 3px
  );
}

.timeline-container::after {
  content: '';
  position: absolute;
  top: 0; left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0;
  pointer-events: none;
  z-index: 5;
  transition: opacity 15s ease-in-out;
}

.timeline-container.fading::after {
  opacity: 1;
}


.timeline-container.flash-frame {
  background: white;
}

.timeline-container.flash-frame {
  background: white;
}

@keyframes horizontalGlitch {
  0%   { transform: translateX(0); }
  20%  { transform: translateX(4px); }
  40%  { transform: translateX(-4px); }
  60%  { transform: translateX(2px); }
  80%  { transform: translateX(-2px); }
  100% { transform: translateX(0); }
}

.timeline-container.horizontal-glitch {
  animation: horizontalGlitch 0.4s ease-in-out;
}

@keyframes screenTear {
  0%   { transform: translateY(0); }
  5%   { transform: translateY(-10px); }
  10%  { transform: translateY(10px); }
  15%  { transform: translateY(0); }
  100% { transform: translateY(0); }
}

.timeline-container.screen-tear {
  animation: screenTear 0.4s ease-in-out;
}


.starfield {
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 0;
  opacity: 0; /* start hidden */
  animation: fadeInStars 60s ease-in-out forwards;
  animation-delay: 0.5s; /* optional delay */
}


.star {
  position: absolute;
  width: 2px;
  height: 2px;
  background: rgb(124, 101, 129);
  border-radius: 50%;
  opacity: 0.8;
  animation: starTwinkle 4s infinite alternate;
}

@keyframes starTwinkle {
  from { opacity: 0.1; }
  to { opacity: 2; }
}

.special-star {
  position: absolute;
  top: 30%;
  left: 58%;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: rgb(100, 0, 214);
  z-index: 1;
  transition: background-color 3s, opacity 3s;
}

.special-star.pulsing {
  animation: pulse 4s infinite;
}

.special-star.faded {
  opacity: 0;
  animation: none;
}

.special-star.blue {
  background-color: #4099ff;
  opacity: 1.5;
  animation: none;
}

@keyframes pulse {
  0% { opacity: 0.2; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.5); }
  100% { opacity: 0.2; transform: scale(1); }
}

.entry {
  position: absolute;
  max-width: 600px;
  z-index: 2;
  animation: fadeIn 1s ease-in;
  overflow: visible;
}

.entry-0 { top: 10%; left: 10%; }
.entry-1 { top: 20%; left: 60%; }
.entry-2 { top: 35%; left: 30%; }
.entry-3 { top: 50%; left: 65%; }
.entry-4 { top: 70%; left: 20%; }

.date-line {
  position: relative;
  color: #8f8f8f;
  font-family: 'Share Tech Mono', monospace;
  font-weight: bold;
  font-size: 1.1rem;
  display: inline-block;
  white-space: nowrap;
  overflow: visible;
  padding-right: 10px;
}

.date-line::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 1px;
  background-color: #b6b6b6;
  animation: blink-caret 0.75s step-end infinite;
}

@keyframes blink-caret {
  0%, 100% { opacity: 0; }
  50% { opacity: 1; }
}


@keyframes textFlicker {
  0% { opacity: 0; }
  10% { opacity: 1; }
  15% { opacity: 0.4; }
  20% { opacity: 1; }
  100% { opacity: 1; }
}

.text-line p {
  animation: textFlicker 0.6s ease-in-out;
}


.text-line p {
  color: #b6b6b6;
  opacity: 0.85;
  text-shadow: 0 0 2px #444;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInStars {
  from { opacity: 0; }
  to { opacity: 1; }
}
.stars-only {
  opacity: 0;
  animation: fadeInStars 2.5s ease-in-out forwards;
  animation-delay: 0.5s;
}

@keyframes fadeInStars {
  from { opacity: 0; }
  to { opacity: 1; }
}

